import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import GolvLaggningCarousel from "../components/golvlaggningCarousel"
import { useStaticQuery, graphql } from "gatsby"
import { ParallaxProvider } from "react-scroll-parallax"
import ParallaxBanner from "../components/parallaxBanner"




const GolvLaggningPage = () => {
    const { gcms } = useStaticQuery(graphql`
    {
        gcms {
            golvlaggningHeroes {
                hero {
                  url
                }
              }
        }
    }`)

    return (<Layout>
        <SEO title="Golvläggning" />
        <ParallaxProvider>
            <div class="cntnr column-flex" >
                <ParallaxBanner url={gcms.golvlaggningHeroes[0].hero.url} heading="Golvläggning" />
                <section class="content-section">
                <h1 class="section-header">Golvläggning</h1><br/>
                    <p>
                        Vi på victoria golv har ett öga för detaljer och mångårig erfarenhet av golvläggning.
                        Vi hjälper dig att lägga alla typer av golv, från lamellgolv, furu och vinyl till vacker
                        fiskbensparkett och massiva trägolv i flera mönster. Vid behov utför vi såklart
                        även undergolv-arbetet inför golvläggningen.
                 </p>
                    <p>Varmt välkomna att kontakta oss via formuläret på sidan eller ring <a style={{ color: "black" }} href="tel:08-999017">08-999017</a> för offert.
                Vid behov utför vi ett kostnadsfritt hembesök.
                </p>

                </section>

                <section class="slide-section">
                    <GolvLaggningCarousel />
                </section>
            </div>
        </ParallaxProvider>
    </Layout>
    )
}

export default GolvLaggningPage;